<template>
	<div class="Lotterypool_box">

		<!-- <div class="Lotterypool_head">
			<div class="Lotterypool_head_left">
				<p data-v-7cecb499="" style="line-height: 50px; float: left;">中奖总人数
				<span data-v-7cecb499="" style="font-size: 12px; color: rgb(102, 102, 102); margin-left: 5px;">(共5条记录)</span></p>
			</div>
		</div> -->

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="p_id" label="用户ID" align='center' >
			</el-table-column>
			<el-table-column prop="prize_name" label="名称" align='center' >
			</el-table-column>
			<el-table-column label="已中奖" align='center'>
				<template slot-scope="scope">
					<el-button type="text" v-if="scope.row.is_winning==1">是</el-button>
					<el-button type="text" v-else disabled>否</el-button>
				</template>
			</el-table-column>
		</el-table>

		<pagination :currentPage="currentPage" :total="total" :limit="pagesize" :small="true"
		     @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange"/>

	</div>
</template>

<script>
	import pagination from '../../components/Pagination.vue'
	export default {
		data() {
			return {
				currentPage: 1,
				pagesize: 10,
				total: 0,
				tableData: [],
			}
		},
		components:{
			pagination
		},
		created() {
			this.All_data(this.currentPage, this.pagesize)
		},
		methods: {
			handleCurrentChange(val) {
				this.currentPage = val;
				this.All_data(this.currentPage, this.pagesize);
			},
			handleSizeChange(val) {
				this.pagesize = val;
				this.currentPage = 1; //这里选择每页多少数据，重置页码为1
				this.All_data(this.currentPage, this.pagesize);
			},
			All_data(page, size) {
				let data = {
					"is_winning":1,
					"is_draw":1,
					"num": page,
					"size": size
				}
				this.tableData = []
				this.$axios.post(this.$url + '/choujiang/searchPage',data)
				  .then((res) => {
				    // console.log(res.data.pojo.list)
				    this.total = res.data.pojo.total
				    this.tableData = res.data.pojo.list
				  })
				  .catch((res) => {
				    console.log(res)
				  })
			},
		}
	}
</script>

<style scoped>
	.Lotterypool_box {
		width: 100%;
		height: auto;
		/* background: darkcyan; */
	}

	.Lotterypool_head {
		width: auto;
		height: 30px;
		line-height: 30px;
		/* background: darkcyan; */
	}

	.Lotterypool_head_left {
		height: 30px;
		/* background-color: darkgreen; */
		line-height: 30px;
	}

	.Lotterypool_head_right {
		margin-right: 30px;
	}
</style>



